@import "variables.less";

.next-step {
  color: @mainColor;
  font-size: 35px;
  float: right;
  text-decoration: underline;
}

nav li.active {
  font-weight: bold;
  color: @mainColor;
}

table.database-setup {
  tr {
	display:none;

	&[data-databases~='mysql'] {
	  display:table-row;
	}
  }

  td {
	vertical-align: middle !important;
  }
}